<template>
  <el-container class="mainContainer">
    <el-aside class="SideBarContainer"
      ><SideBar
        :sideBarList="
          userRooms.map((r) => ({ text: r.title, url: '/room/' + r.id + '/' }))
        "
        :urlMode="true"
    /></el-aside>
    <el-main class="chatRoomContainer">
      <ChatRoom
        :roomws="roomws"
        :messages="messages"
        v-on:addMessage="addMessage"
      />
    </el-main>
    <el-aside class="roomInfo">
      <el-scrollbar>
        <h5 class="title">{{ room.title }}</h5>
        <el-divider></el-divider>
        <div>
          <h5></h5>
          <p>{{ host ? host : '' }}</p>
        </div>
        <el-divider></el-divider>
        <div>
          <h5></h5>
          <p>
            {{ !room.create_time ? '' : room.create_time.slice(0, 10) }}
            {{ !room.create_time ? '' : room.create_time.slice(11, 19) }}
            ~<br />
            {{ !room.valid_time ? '' : room.valid_time.slice(0, 10) }}
            {{ !room.valid_time ? '' : room.valid_time.slice(11, 19) }}
          </p>
        </div>
        <el-divider></el-divider>
        <div>
          <h5>
            <i
              class="el-icon-edit el-input__icon"
              @click="dialogFormVisible = true"
            ></i>
          </h5>
          <p>{{ room.introduction }}</p>
        </div>
        <el-divider></el-divider>
        <div>
          <h5></h5>
          <el-button size="mini" round>{{ room.room_category }}</el-button>
        </div>
        <el-divider></el-divider>
        <div>
          <h5>
            <i
              class="el-icon-edit el-input__icon"
              @click="memberFormVisible = true"
            ></i>
          </h5>
          <div
            class="member"
            v-for="userObj in memberList"
            :key="userObj.member_id"
          >
            <el-avatar size="small" icon="el-icon-user-solid"></el-avatar>
            <p class="username">{{ userObj.nickname }}</p>
          </div>
        </div>
        <el-divider></el-divider>
        <div>
          <h5>
            
            <i class="el-icon-edit el-input__icon" @click="handleIconClick"></i>
          </h5>
          <p>{{ room.room_type == 'lesson' ? '' : room.people_limit }}</p>
        </div>
        <el-divider></el-divider>
        <div>
          <h5> input</h5>
          <el-input placeholder="" clearable>
            <template #suffix>
              <i class="el-icon-edit el-input__icon" @click="handleIconClick">
              </i>
            </template>
          </el-input>
        </div>
        <el-divider></el-divider>
        <div>
          <h5></h5>
          <el-popconfirm
            confirmButtonText=""
            confirmButtonType="danger"
            cancelButtonText=""
            icon="el-icon-info"
            iconColor="red"
            title=" ? ''"
            @confirm="handleExitRoom"
          >
            <template #reference>
              <el-button type="danger" round></el-button>
            </template>
          </el-popconfirm>
        </div>
      </el-scrollbar>
    </el-aside>
  </el-container>
  <el-dialog title="" v-model="dialogFormVisible" v-if="isAdmin">
    <el-form :model="dialogFormRoom">
      <el-form-item label="">
        <el-input v-model="dialogFormRoom.title"></el-input>
      </el-form-item>
      <el-form-item label=" Category">
        <el-select
          v-model="dialogFormRoom.room_category"
          placeholder=""
        >
          <el-option
            v-for="(item, key) in categoryDict"
            :label="item"
            :key="item"
            :value="key"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input
          type="textarea"
          placeholder="Enter description of your room:"
          v-model="dialogFormRoom.introduction"
          maxlength="100"
          show-word-limit
        >
        </el-input>
      </el-form-item>
      <el-form-item @submit.prevent>
        <span></span>
        <el-slider
          v-model="dialogFormRoom.people_limit"
          show-input
          :max="100"
          :step="5"
          input-size="mini"
        >
        </el-slider>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogFormVisible = false"></el-button>
        <el-button
          type="primary"
          @click=";[(handleEditRoom(), (dialogFormVisible = false))]"
          ></el-button
        >
      </span>
    </template>
  </el-dialog>
  <el-dialog v-model="memberFormVisible">
    <h3></h3>
    <el-table :data="memberList" style="width: 100%">
      <el-table-column type="expand">
        <template #default="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.username }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.nickname }}</span>
            </el-form-item>
            <el-form-item
              label=""
              v-if="
                isAdmin &&
                user.member_id != props.row.member_id &&
                props.row.member_id !=
                  memberList.find((m) => m.access_level == 'admin').member_id
              "
            >
              <el-button
                size="mini"
                type="warning"
                plain
                @click="
                  handleMemberOperation(props.$index, props.row, 'remove')
                "
                ></el-button
              >
              <el-button
                size="mini"
                type="danger"
                @click="handleMemberOperation(props.$index, props.row, 'block')"
                ></el-button
              >
              <el-popconfirm
                confirmButtonText=""
                cancelButtonText=""
                icon="el-icon-info"
                iconColor="red"
                title=" ? "
                @confirm="handleTransferAdmin(props.row)"
              >
                <template #reference>
                  <el-button size="mini" type="danger" plain
                    ></el-button
                  >
                </template>
              </el-popconfirm>
            </el-form-item>
            <el-form-item
              label=":"
              v-if="
                isAdmin &&
                user.member_id != props.row.member_id &&
                props.row.member_id !=
                  memberList.find((m) => m.access_level == 'admin').member_id
              "
            >
              <el-select
                v-model="tmp"
                @change="handleSetLevel($event, props.row.member_id)"
              >
                <el-option
                  v-for="(val, key) in { manager: '', user: '' }"
                  :key="key"
                  :label="val"
                  :value="key"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="" prop="username"> </el-table-column>
      <el-table-column label="" prop="nickname"></el-table-column>
      <el-table-column label="" prop="access_level"></el-table-column>
    </el-table>
    <h3>Block </h3>
    <el-table :data="blockList" style="width: 100%">
      <el-table-column type="expand">
        <template #default="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{
                memberList.filter(
                  (m) => m.member_id == props.row.block_manager_id
                )[0].nickname +
                '(' +
                props.row.manager_username +
                ')'
              }}</span>
            </el-form-item>
            <el-form-item label="" v-if="isAdmin">
              <el-popconfirm
                confirmButtonText=""
                cancelButtonText=""
                icon="el-icon-info"
                iconColor="red"
                title=" ?"
                @confirm="handleUnblockOperation(props.row)"
              >
                <template #reference>
                  <el-button size="mini" type="danger"></el-button>
                </template>
              </el-popconfirm>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="user_username" label=""> </el-table-column>
      <el-table-column prop="reason" label=""></el-table-column>
    </el-table>
    <h3></h3>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="invitedUserId" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleInvitation"></el-button>
      </el-form-item>
    </el-form>
    <el-table :data="invitationList" style="width: 100%">
      <!-- <el-table-column type="expand">
        <template #default="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="" v-if="isAdmin">
              <el-popconfirm
                confirmButtonText=""
                cancelButtonText=""
                icon="el-icon-info"
                iconColor="red"
                title=" ?"
                @confirm="handleCancelInvitation(props.row)"
              >
                <template #reference>
                  <el-button size="mini" type="danger"></el-button>
                </template>
              </el-popconfirm>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column> -->
      <el-table-column
        prop="invited_username"
        label=""
      ></el-table-column>
      <el-table-column prop="inviter_username" label=""></el-table-column>
    </el-table>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="memberFormVisible = false"
          ></el-button
        >
      </span>
    </template>
    <h3>Room Record</h3>
    <div>
      <span
        v-for="rec in roomRecord.slice(
          roomRecord.length - 5,
          roomRecord.length
        )"
        :key="rec"
        >{{ rec.recording }}<br
      /></span>
    </div>
  </el-dialog>
  <el-dialog
    title=""
    v-model="removeDialog.visible"
    width="30%"
    :before-close="handleClose"
  >
    <el-result
      icon="warning"
      :title="removeDialog.title"
      :subTitle="removeDialog.subTitle"
    >
      <template #extra>
        <el-button type="primary" size="medium"></el-button>
      </template>
    </el-result>
    <template #footer>
      <span class="dialog-footer"> </span>
    </template>
  </el-dialog>
</template>

<script>
import RoomService from '@/services/RoomService.js'
import SideBar from '@/components/SideBar.vue'
import ChatRoom from '@/components/ChatRoom.vue'
import WsService from '@/services/WebsocketService.js'

export default {
  props: ['id'],
  components: {
    SideBar,
    ChatRoom,
  },
  data() {
    return {
      exist_btn_text: '',
      tmp: '',
      invitedUserId: '',
      user: {
        access_level: 'user',
      },
      memberList: [],
      invitationList: [],
      blockList: [],
      categoryDict: {},
      userRooms: [],
      levelDict: { admin: '', manager: '', user: '' },
      host: '',
      roomRecord: '',
      dialogFormVisible: false,
      isAdmin: false,
      room: {
        id: 0,
        title: 'Loading Not Successful',
        introduction: 'Loading Not Successful',
        create_time: '2000-01-01T20:33:33.033448+08:00',
        valid_time: '2000-01-01T20:33:33.033448+08:00',
        room_type: 'private',
        room_category: 'course',
        people_limit: 1,
      },
      tableData: [
        {
          date: '2010-01-01',
          name: '',
          address: '',
        },
      ],
      dialogFormRoom: {},
      memberFormVisible: false,
      roomws: {},
      messages: [],
      removeDialog: { visible: false, title: '', subTitle: '' },
    }
  },
  async created() {
    if (this.$store.state.token && !this.$store.state.is_verify) {
      this.$store.dispatch('getIsVerify').then(() => {
        console.log(' mail  !')
        if (this.$store.state.is_verify == false) {
          this.$router.push({
            name: 'profile',
          })
          this.$message.error(' mail  !')
        }
      })
    }

    if (this.$store.state.token) {
      this.$store
        .dispatch('refreshToken')
        .then((resRefresh) => {
          console.log(resRefresh)
          return this.getUserRooms()
        })
        .then((res) => {
          if (res.data.map((ro) => ro.id).includes(Number(this.id))) {
            this.userRooms = res.data
            return this.init_list()
          } else {
            this.$message.error(' = =')
            this.removeDialog.title = ''
            this.removeDialog.subTitle = ': /'
            this.removeDialog.visible = true
            setTimeout(() => {
              this.$router.push({
                name: 'home',
              })
            }, 2000)
            return Promise.reject('user not in room !')
          }
        })
        .catch((err) => {
          // if refresh token is invalid, it will trigger 401 and redirect to Login auto.
          // so here we don't need to handle it.
          console.log(err)
        })
    } else {
      this.$message.error(' !')
      this.$router.push({
        name: 'login',
      })
    }
    //---------------------websocket-------------------------------

    if (this.roomws[parseInt(this.$route.params.id, 10)] == null) {
      console.log('this.roomws[this.$route.params.id] == null')
      let nowRoomID = parseInt(this.$route.params.id, 10)
      this.roomws[nowRoomID] = WsService.InitRoomWebsocket(
        this.$store.state.token,
        this.$route.params.id
      ) //
      console.log(this.roomws[nowRoomID])

      this.roomws[nowRoomID].onmessage = (event) => {
        //console.log(event.data)

        let res = JSON.parse(event.data) //data
        let showtime = null
        console.log('[Websocket event.data]:', event.data)
        // console.log(res.roomID) //room ID
        // console.log(res.message) //updatemessage
        // console.log(res.time) //

        //-------header---------
        // message = [ 'memberList', 'invitationList', 'blockList', 'RoomProfile' ...  ]
        switch (res.header) {
          case 'message': //-----header-------
            // console.log(res.userID, res.roomID, res.nickname, res.message)
            showtime = new Date(res.time) //5. jsParse
            var hour = showtime.getHours()
            var minute = showtime.getMinutes()
            hour = String(hour).padStart(2, '0')
            minute = String(minute).padStart(2, '0')
            var currentTime = `${hour}:${minute}`
            var newMessage = {
              user: res.userID,
              text: res.message,
              time: currentTime,
            }
            this.messages.push(newMessage)
            //------------------------------------
            /* codeElement UIVue
            console.log(this.$store.state.user_id)
            if (res.userId != this.$store.state.user_id) {
              this.messages.push({
                id: res.userID,
                user: res.username,
                time: String(showtime.getHours()) + String(showtime.getMinutes()),
                text: res.message,
              })
            }
            */
            break
          case 'remove':
            if (res.message === 'Removed') {
              console.log('ws case : remove')

              let msgObj = {
                header: 'controlMessage', // Message  header,messageheadermessage
                msg_type: 'text', // Message  type, ,typetext
                userID: this.$store.state.user_id, //messageuser ID, store
                roomID: parseInt(this.$route.params.id, 10), // roomID,routeID
                message: 'Close', //
                token: this.$store.state.token, //Request token
              }
              this.roomws[nowRoomID].send(JSON.stringify(msgObj))

              //------------------------------------------
              //Then Close Room Handle or Trigger
              this.removeDialog.title = ''
              this.removeDialog.subTitle = ': '
              this.removeDialog.visible = true
              setTimeout(() => {
                this.$router.push({
                  name: 'home',
                })
              }, 3000)
            }
            break
          case 'update':
            if (res.message.includes('member_list')) {
              this.getMemberList()
                .then((res) => {
                  this.memberList = res.data
                  this.host = res.data.filter(
                    (user) => user.access_level == 'admin'
                  )[0].nickname
                  if (!res.data.filter((m) => m.member_id)) {
                    console.log('you are not in room QQ')
                  }
                })
                .catch((err) => {
                  console.log(err.data)
                })
            }
            if (res.message.includes('block_list')) {
              this.getBlockList()
                .then((res) => {
                  this.blockList = res.data
                  console.log('blockList:', res.data)
                })
                .catch((err) => {
                  console.log(err.data)
                })
            }
            if (res.message.includes('invite_list')) {
              this.getInvitationList()
                .then((res) => {
                  this.invitationList = res.data
                  console.log('invitationList', res.data)
                })
                .catch((err) => {
                  console.log(err.data)
                })
            }
            if (res.message.includes('profile')) {
              this.getRoomObj()
                .then((res) => {
                  this.room = res
                  this.dialogFormRoom = { ...res }
                  console.log(res)
                })
                .catch((err) => {
                  console.log(err)
                })
            }

            if (res.message === 'delete_room') {
              // = if (res.message.includes('delete_room'))
              let msgObj = {
                header: 'controlMessage', // Message  header,messageheadermessage
                msg_type: 'text', // Message  type, ,typetext
                userID: this.$store.state.user_id, //messageuser ID, store
                roomID: parseInt(this.$route.params.id, 10), // roomID,routeID
                message: 'Close', //
                token: this.$store.state.token, //Request token
              }
              this.roomws[nowRoomID].send(JSON.stringify(msgObj))
              //----------------Then Close Room Handle or Trigger --------------------------
              this.removeDialog.title = ''
              this.removeDialog.subTitle = ': '
              this.removeDialog.visible = true
              setTimeout(() => {
                this.$router.push({
                  name: 'home',
                })
              }, 3000)
            }
            break
          case 'ping': //websocket
            if (this.$route.fullPath != '/room/' + nowRoomID) {
              this.roomws[nowRoomID].close()
            }
            break
        }
      }
    }

    //--------------------websocket-------------------------------
  },
  methods: {
    init_list() {
      this.getRoomObj()
        .then((res) => {
          this.room = res
          this.dialogFormRoom = { ...res }
          console.log(res)
        })
        .catch((err) => {
          console.log(err)
        })

      this.getInvitationList()
        .then((res) => {
          this.invitationList = res.data
          console.log('invitationList', res.data)
        })
        .catch((err) => {
          console.log(err.data)
        })

      this.getMemberList()
        .then((res) => {
          this.memberList = res.data
          this.host = res.data.filter(
            (user) => user.access_level == 'admin'
          )[0].nickname
          console.log('memberList: ', res.data)
          return this.getUserObj()
        })
        .catch((err) => {
          console.log(err.data)
        })

      this.getBlockList()
        .then((res) => {
          this.blockList = res.data
          console.log('blockList:', res.data)
        })
        .catch((err) => {
          console.log(err.data)
        })

      this.getInvitationList()
        .then((res) => {
          this.invitationList = res.data
          console.log('invitationList:', res.data)
        })
        .catch((err) => {
          console.log(err.data)
        })

      this.getRoomRecord()
        .then((res) => {
          this.roomRecord = res.data
          console.log('roomRecord:', res.data)
        })
        .catch((err) => {
          console.log(err.data)
        })

      this.getRoomCategory()
        .then((res) => {
          this.categoryDict = res.data
          console.log('categoryDict:', res.data)
        })
        .catch((err) => {
          console.log(err.data)
        })
    },
    getUserObj() {
      RoomService.getUserId()
        .then((res) => {
          let userId = res.data.id
          let userObj = this.memberList.filter((m) => m.member_id == userId)[0]
          if (userObj) {
            this.user = userObj
          }
        })
        .catch((err) => {
          return Promise.reject(err)
        })
    },
    getRoomObj() {
      return new Promise((resolve, reject) => {
        RoomService.getRoom(this.id)
          .then((response) => {
            resolve(response.data)
          })
          .catch((error) => {
            console.log('There was an error:', JSON.stringify(error.response))
            reject(error.data) //
          })
      })
    },
    getMemberList() {
      return RoomService.getRoomMemberList(this.id)
    },
    getBlockList() {
      return RoomService.getRoomBlockList(this.id)
    },
    getInvitationList() {
      return RoomService.getRoomInvitationList(this.id)
    },
    getRoomRecord() {
      return RoomService.getRoomRecord(this.id)
    },
    getRoomCategory() {
      return RoomService.getRoomCategory()
    },
    getUserRooms() {
      return RoomService.getUserRooms()
    },
    handleEditRoom() {
      RoomService.putRoom(this.id, this.dialogFormRoom)
        .then((res) => {
          console.log('[Success] :', res.data)
          this.room = res.data
        })
        .catch((err) => {
          console.log(err)
        })
    },
    handleExitRoom() {
      if (this.user.access_level == 'admin') {
        RoomService.deleteRoom(this.id).catch((err) => {
          this.$message.error(err.data.error)
        })
      } else {
        RoomService.deleteLeaveRoom(this.id).catch((err) => {
          this.$message.error(err.data.error)
        })
      }

      this.$router.push({
        name: 'home',
      })
      return
    },
    handleInvitation() {
      RoomService.postRoomInvite(this.id, this.invitedUserId)
        .then(() => {
          console.log('postRoomInvite Successful')
          return this.getInvitationList()
        })
        .then((res) => {
          this.invitationList = res.data
        })
        .catch((err) => {
          this.$message.error(err.data.error)
        })
    },
    handleCancelInvitation(row) {
      console.log(row.invited_id)
    },
    handleSetLevel(newLevel, userId) {
      RoomService.putSetLevel(this.id, { [userId]: newLevel })
        .then(() => {
          return this.getMemberList()
        })
        .then((res) => {
          this.memberList = res.data
          console.log('[Success]', userId, '', newLevel)
          this.$message.success(' !')
        })
        .catch((err) => {
          this.$message.error('handleSetLevel:', err.data.error)
        })
    },
    handleAddModerator(row) {
      let parameter = {}
      parameter[row.member_id] = ''
      console.log(parameter)
    },
    handleTransferAdmin(row) {
      RoomService.putTransferAdmin(this.id, row.member_id)
        .then(() => {
          console.log('')
        })
        .catch((err) => {
          console.log(err)
        })
    },
    handleUnblockOperation(row) {
      RoomService.deleteUnblockUser(this.id, row.blocked_user_id)
        .then(() => {
          console.log('[Success] :', row.blocked_user_id)
          this.blockList = this.blockList.filter(
            (m) => m.blocked_user_id != row.blocked_user_id
          )
        })
        .catch((err) => {
          console.log(err)
        })
    },
    handleMemberOperation(id, row, operation) {
      var operationText = operation == 'block' ? '' : ''
      this.$prompt(
        '',
        ' ' + row.nickname + ' ' + operationText,
        {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /[^(?!\s*$).+]/,
          inputErrorMessage: ' !',
        }
      )
        .then((val) => {
          if (operation == 'block') {
            return RoomService.postBlockUser(this.id, row.member_id, val.value)
          } else if (operation == 'remove') {
            return RoomService.deleteRemoveUser(
              this.id,
              row.member_id,
              val.value
            )
          }
        })
        .then((res) => {
          if (res.status == 200) {
            this.$message({
              type: 'success',
              message: operationText + '',
            })
          }
          this.memberList = this.memberList.filter(
            (member) => member.member_id != row.member_id
          )
          if (operation == 'block') {
            return this.getBlockList()
              .then((res) => {
                this.blockList = res.data
              })
              .catch((err) => {
                console.log(err.data)
              })
          }
        })
        .catch((err) => {
          console.log('[In RoomShow]', err)
          if (err == 'cancel') {
            this.$message({
              type: 'info',
              message: '',
            })
          }
        })

      console.log(
        '[Success]: ',
        operation,
        ':',
        row.member_id
      )
    },
    addMessage(newMessage) {
      this.messages.push(newMessage)
    },
  },
  watch: {
    memberList: function (_memberList) {
      this.getUserObj()
      console.log('[watch] memberList changed:', _memberList)
    },
    user: function (_user) {
      console.log('[watch] user changed:', _user)
      this.isAdmin =
        _user.access_level == 'admin' || _user.access_level == 'manager'
          ? true
          : false
    },
  },
}
</script>
